@mixin removePaddingRight($value){
  padding-right: $value !important;
}

@mixin removePaddingLeft($value){
  padding-left: $value !important;
}

@mixin removePaddingBottom($value){
  padding-bottom: $value !important;
}

@mixin removePadding($value){
  padding: $value !important;
}

.table {

  thead, tbody, tfoot {
    tr {
      th:last-child { @include removePaddingRight(0); }
      th:first-child { @include removePaddingLeft(0); }
      th { @include removePadding(3px); }
      td:last-child { @include removePaddingRight(0); }
      td:first-child { @include removePaddingLeft(0); }
      td { @include removePadding(3px); }
    }
    tr:last-child {
      td {
        @include removePaddingBottom(5px)
      }
    }
  }

  /* Header table */
  thead {
    tr:first-child {
      th {
        background-color: #EDECEC !important;
        padding: 5px 0 !important;
      }
    }
  }

  md-checkbox {
    display: inline;
    line-height: 18px !important;
    @include removePaddingLeft(0);

    .md-icon {
      width: 13px;
      height: 13px;
      top: 2px;
      left: 0;
    }

  }

  md-input-container {
    @include removePaddingBottom(0);
  }

  md-checkbox.md-checked {
    .md-icon:after {
      left: 4px !important;
      top: 1px !important;
      width: 5px !important;
      height: 7pt !important;
    }

  }

}

.table > thead > tr >th > md-checkbox,
.table > tbody > tr > th > md-checkbox,
.table > tfoot > tr >th > md-checkbox,
.table > thead > tr > td > md-checkbox,
.table > tbody > tr > td > md-checkbox,
.table > tfoot > tr > td > md-checkbox {
  margin: 0 !important;
}
